import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'changNumberToChinese'
})
export class ChangNumberToChinesePipe implements PipeTransform {
  transform(value: number): string {
    switch (value) {
      case 1:
        return 's 1';
      case 2:
        return 's 2';
      case 3:
        return 's 3';
      case 4:
        return 's 4';
      case 5:
        return 's 5';
      case 6:
        return 's 6';
      case 7:
        return 's 7';
      case 8:
        return 's 8';
      case 9:
        return 's 9';
      case 10:
        return 's 10';
      default:
        return '';
    }
  }
}
