
import {
  HttpErrorResponse,
  HttpEvent,
  HttpHandler,
  HttpHeaders,
  HttpInterceptor,
  HttpRequest
} from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError, filter } from 'rxjs/operators';

import { TokenKey } from '@config/constant';
import { NzSafeAny } from 'ng-zorro-antd/core/types';
import { NzMessageService } from 'ng-zorro-antd/message';

import { WindowService } from '../common/window.service';
import * as Const from '@app/common/const';
interface CustomHttpConfig {
  headers?: HttpHeaders;
}

@Injectable()
export class HttpInterceptorService implements HttpInterceptor {
  constructor(private windowServe: WindowService, public message: NzMessageService) {}

  intercept(req: HttpRequest<NzSafeAny>, next: HttpHandler): Observable<HttpEvent<NzSafeAny>> {
    const token = this.windowServe.getSessionStorage(TokenKey);
    let httpConfig: CustomHttpConfig = {};
    if (!!token) {
      httpConfig = { headers: req.headers.set(TokenKey, token) };
    }
    const copyReq = req.clone(httpConfig);
    if(copyReq.url == Const.tinhthanhApi) {
      return next.handle(req);
    } 
    return next.handle(copyReq).pipe(
      filter(e => e.type !== 0),
      catchError(error => this.handleError(error))
    );
  }

  private handleError(error: HttpErrorResponse): Observable<never> {
    const status = error.status;
    let errMsg = '';
    if (status === 0) {
      errMsg = ' xy ra li mng khng xc nh, vui lng kim tra mng ca bn.';
      this.message.info(' xy ra li mng khng xc nh, vui lng kim tra mng ca bn.');
    }
    if (status >= 300 && status < 400) {
      errMsg = `Yu cu  c chuyn hng bi my ch vi m trng thi ${status}`;
      this.message.info(`Yu cu  c chuyn hng bi my ch vi m trng thi ${status}`);
    }
    if (status >= 400 && status < 500) {
      errMsg = `My khch b li, c th d liu gi sai, m trng thi ${status}`;
      this.message.info(`My khch b li, c th d liu gi sai, m trng thi ${status}`);
    }
    if (status >= 500) {
      errMsg = ` xy ra li my ch vi m trng thi ${status}`;
      this.message.info(` xy ra li my ch vi m trng thi ${status}`);
    }
    return throwError({
      code: status,
      message: errMsg
    });
  }
}
